<template>
  <p>Test getting some data from actor</p>
</template>


<script>
process.env.NODE_TLS_REJECT_UNAUTHORIZED = "0";
import axios from "axios";
axios.defaults.headers.post["Content-Type"] = "application/json";
//axios.defaults.baseURL = "https://127.0.0.1";
const API = "https://172.17.0.2/web/gedis/http/vuejs";

export default {
  data() {
    var people_info = {};
    console.log("heeeeeere", people_info);
    return {
      people_info
    };
  },
  methods: {
    // call vuejs actor
    async callActorWithArgs(actorCmd, actorArgs) {
      let p = () =>
        axios.post(`${API}/${actorCmd}`, {
          args: actorArgs
        });
      let resp = await p();
      return new Promise((resolve, reject) => resolve(resp.data));
    },

    ping() {
      console.log("PING: ", this.callActorWithArgs("ping", {}));
    },

    get_people() {
      let info = this.callActorWithArgs("get_info", {});
      this.people_info = info;
      console.log(`>> ${info}`);
    }
  },
  mounted() {
    this.callActorWithArgs();
    this.get_people();
    this.ping();
  }
};
</script>
